import parse from '@/components/jyf-Parser/index.vue';
import iAbnor from '@/components/i-abnor/i-abnor.vue';

export default {
	components: {
		parse,
		iAbnor
	},
	props: {
		count: {
			type: Number,
			default: 0
		},
		listData: {
			type: Array,
			default: function() {
				return [];
			}
		}
	},
	data() {
		return {
			list: this.listData
		};
	}
};
